$cell-size: 100px;
$calendar-gap: 1px;
$calendar-paddings: 10px;
$calendar-width:
  ((($cell-size + $calendar-gap) * 7) + ($calendar-paddings * 2));

$first-day-margin:
  "mon" 0,
  "tue" $cell-size + $calendar-gap,
  "wed" ($cell-size + $calendar-gap) * 2,
  "thu" ($cell-size + $calendar-gap) * 3,
  "fri" ($cell-size + $calendar-gap) * 4,
  "sat" ($cell-size + $calendar-gap) * 5,
  "sun" ($cell-size + $calendar-gap) * 6;
